import React, { useState, useEffect } from 'react';
import { Button, Checkbox, Form } from 'semantic-ui-react'
import axios from 'axios';
import { useNavigate } from 'react-router';

export default function Update() {
    let navigate = useNavigate();
    const [phoneNumber, setPhoneNumber] = useState(null);
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [domain, setDomain] = useState('');
    const [status, setStatus] = useState('');

    useEffect(() => {
        setPhoneNumber(localStorage.getItem('Phone Number'))
        setUsername(localStorage.getItem('Username'));
        setPassword(localStorage.getItem('Password'));
        setDomain(localStorage.getItem('Domain'));
        setStatus(localStorage.getItem('Status'));
    }, []);

    const updateAPIData = () => {
        axios.put(`https://6501cdc9736d26322f5c5038.mockapi.io/ims/subscriber/${phoneNumber}`, {
            username,
            password,
            domain,
            status,
        }).then(() => {
            navigate('/')
        })
    }
    return (
        <div>
            <h4>Phone Number: {phoneNumber}</h4>
            <Form className="update-form">
                <Form.Field>
                    <label>User Name</label>
                    <input placeholder='User Name' value={username} onChange={(e) => setUsername(e.target.value)}/>
                </Form.Field>
                
                <Form.Field>
                    <label>Password</label>
                    <input placeholder='Password' value={password} onChange={(e) => setPassword(e.target.value)}/>
                </Form.Field>

                <Form.Field>
                    <label>Domain</label>
                    <input placeholder='Domain' value={domain} onChange={(e) => setDomain(e.target.value)}/>
                </Form.Field>

                <Form.Field>
                    <label>Status</label>
                    <input placeholder='Status' value={status} onChange={(e) => setStatus(e.target.value)}/>
                </Form.Field>

                <Button type='submit' onClick={updateAPIData}>Update</Button>
            </Form>
        </div>
    )
}
