import React, { useState, useEffect } from 'react';
import { Button, Form, Table, Modal } from 'semantic-ui-react'
import { Link } from 'react-router-dom';
import axios from 'axios';
import { useNavigate } from 'react-router';
 

export default function Search() {
    let navigate = useNavigate();
    const [APIData, setAPIData] = useState([]);
    const [phoneNumber, setPhoneNumber] = useState('');   
    const [notifMessage, setNotifMessage] = useState('');   
    const [displayConfirmationModal, setDisplayConfirmationModal] = useState(false); 
    const [deleteMessage, setDeleteMessage] = useState(null);

    useEffect(() => {
        getAllData();
    }, []);

     const setData = (data) => {
        let { phoneNumber, username, password, domain, status } = data;
        localStorage.setItem('Phone Number', phoneNumber);
        localStorage.setItem('Username', username);
        localStorage.setItem('Password', password);
        localStorage.setItem('Domain', domain);
        localStorage.setItem('Status', status);
    }

    const getAllData = () => {
        setAPIData([]);
        setNotifMessage('');
        axios.get(`https://6501cdc9736d26322f5c5038.mockapi.io/ims/subscriber/`)
            .then((response) => {
                console.log(response.data)
                setAPIData(response.data);
            })
    }

    const getDataByPhoneNumber = () => {
        setAPIData([]);
        setNotifMessage('');
        axios.get(`https://6501cdc9736d26322f5c5038.mockapi.io/ims/subscriber/${phoneNumber}`)
            .then((response) => {
                setAPIData([response.data]);
            })
            .catch((error) => {
                if (error.response) {
                    setNotifMessage('Subscriber not found');
                }
            });
    }

    const showDeleteModal = (phoneNumber) => { 
        setDeleteMessage(`Are you sure you want to delete the Phone Number ${phoneNumber} ?`);        
        setDisplayConfirmationModal(true);
      };
    
    const hideConfirmationModal = () => {
        setDisplayConfirmationModal(false);
    };

    const onDelete = (phoneNumber) => {
        axios.delete(`https://6501cdc9736d26322f5c5038.mockapi.io/ims/subscriber/${phoneNumber}`)
        .then(() => {
            getAllData();
            setDisplayConfirmationModal(false);
        })
    }

    return (
        <div>
            <h3 className="search-form__error">{notifMessage}</h3>     
            <Form className="search-form d-flex">  
                <Form.Field className="phone-search">
                    <input required type="number" placeholder='Phone Number' onChange={(e) => setPhoneNumber(e.target.value)}/>
                </Form.Field>                
                <Button onClick={getDataByPhoneNumber} type='submit' disabled={!phoneNumber}>Submit</Button>
            </Form>   

            <Table singleLine>
                <Table.Header>
                    <Table.Row>
                        <Table.HeaderCell>Phone Number</Table.HeaderCell>
                        <Table.HeaderCell>Username</Table.HeaderCell>
                        <Table.HeaderCell>Password</Table.HeaderCell>
                        <Table.HeaderCell>Domain</Table.HeaderCell>
                        <Table.HeaderCell>Status</Table.HeaderCell>
                        <Table.HeaderCell>Actions</Table.HeaderCell>
                    </Table.Row>
                </Table.Header>
                <Table.Body>
                    {APIData.map((data) => {
                        return (
                            <Table.Row>
                                <Table.Cell>{data.phoneNumber}</Table.Cell>
                                <Table.Cell>{data.username}</Table.Cell>
                                <Table.Cell>{data.password}</Table.Cell>
                                <Table.Cell>{data.domain}</Table.Cell>
                                <Table.Cell>{data.status}</Table.Cell>  
                                
                                <Link to='/update'>
                                    <Table.Cell> 
                                        <Button onClick={() => setData(data)}>Update</Button>
                                    </Table.Cell>
                                </Link>   
                                <Table.Cell>
                                    <Modal                                      
                                      trigger={<Button>Delete</Button>}
                                    >                
                                        <Modal.Header>Delete Subscriber</Modal.Header>
                                        <Modal.Content>
                                            <p>Are you sure you want to delete this Subscriber?</p>
                                        </Modal.Content>
                                        <Modal.Content>
                                            <div class="actions">
                                                <Button>Cancel</Button>
                                                <Button onClick={() => onDelete(data.phoneNumber)}>Delete</Button>
                                            </div>
                                        </Modal.Content>                    
                                    </Modal>
                                </Table.Cell>                  
                            </Table.Row>
                        )
                    })}
                </Table.Body>
            </Table>
        </div>
    )
}
